}
.section-title {
  margin-bottom: 51px;
}
@media only screen and (max-width: 991px) {
  .section-title {
    margin-bottom: 40px;
  }
}
.section-title .title {
  font-size: 36px;
  margin-bottom: 13px;
  font-family: "Fredoka One Regular", cursive;
}
@media only screen and (max-width: 575px) {
  .section-title .title {
    font-size: 30px;
  }
}
.section-title p {
  color: #2d2d2d;
}

.bg-img {
  background: no-repeat center center;
  background-size: cover;
}
.bg-img.bg-img-top {
  background-position: top center;
}

.bg-img-cover {
  background-size: cover;
}

.swiper-container {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;
}
.swiper-container-android .swiper-slide, .swiper-wrapper {
    transform: translate3d(0px,0,0);
}

.swiper-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: flex;
    transition-property: transform;
    box-sizing: content-box;
}
.swiper-container-fade .swiper-slide {
    /* pointer-events: none; */
    transition-property: opacity;
}

.swiper-slide {
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    position: relative;
    transition-property: transform;
}
.swiper-container-fade .swiper-slide-active, .swiper-container-fade .swiper-slide-active .swiper-slide-active {
    pointer-events: auto;
}
.bg-img {
  background: no-repeat center center;
  background-size: cover;
}
@keyframes fadeInUp {
  0% {opacity: 0; margin-top: +100%;}
  100% {opacity: 1; margin-top: 0px;}
}
@keyframes fadeInRight {
  0% {opacity: 0; left: 100%;}
  100% {opacity: 1; left: calc(50% + 12px)}
}
@keyframes fadeInLeft {
  0% {opacity: 0; left: 0px;}
  100% {opacity: 1; left: 129px;}
}
.btn-theme {
  background-color: #f379a7;
