  .btn-theme.btn-slide {
    overflow: hidden;
    position: relative;
    z-index: 1;
  }
  .btn-theme.btn-slide:before {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    bottom: -40px;
    content: "";
    height: 65px;
    left: -27px;
    position: absolute;
    width: 65px;
    z-index: -1;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
  }
  .btn-theme.btn-slide:after {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    content: "";
    height: 65px;
    position: absolute;
    right: -27px;
    top: -40px;
    width: 65px;
    z-index: -1;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
  }
  .btn-theme.btn-slide .icon.icon-img {
    right: 42px;
  }
  .btn-theme.btn-slide:hover:before {
    border-radius: 32.5px;
    bottom: 0;
    height: 100%;
    left: 0;
    opacity: 0;
    width: 100%;
  }
  .btn-theme.btn-slide:hover:after {
    border-radius: 32.5px;
    height: 100%;
    opacity: 0;
    right: 0;
    top: 0;
    width: 100%;
  }
